body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #233; }

.App {
  text-align: center; }
  .App__logo {
    margin-top: 30px;
    margin-bottom: 20px;
    height: 40px;
    background: url(/tiktokmood/static/media/tiktoklogo-sign.dafb82c6.svg) center no-repeat; }

.PumButton {
  color: #eed;
  line-height: 44px;
  cursor: pointer;
  letter-spacing: 0.1ch; }
  .PumButton_playing {
    font-weight: bold;
    color: #fff; }

